  .divider {
    width: 100%;
    margin: 20px 0;
    position: relative;
    height: 40px; /* Adjusted height to allow flight icon to move */
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .line {
    width: 100%;
    border-top: 1px dashed #ccc;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }
  
  .flight-animation {
    position: absolute;
    top: 38%;
    left: 0;
    rotate: 45deg;
    transform: translateY(-50%);
    font-size: 20px; /* Adjust font size for visibility */
    animation: fly 15s infinite linear;
  }
  
  @keyframes fly {
    from {
      left: -30px; /* Start the flight animation slightly offscreen */
    }
    to {
      left: calc(100%); /* Move beyond the right edge */
    }
  }
  
