  /* hotel booking.js code */
  .booking-details {
    font-family: Arial, sans-serif;
    padding: 20px;
    background-color: white;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    max-width: 800px;
    margin: 0 auto;
    background-repeat: no-repeat;
  }
  
  .header {
    background-color: #a8e6ee;
    color: rgb(69, 68, 68);
    padding: 10px;
    text-align: center;
    border-radius: 8px 8px 0 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    
  }
  
  .header h1 {
    margin: 0;
    font-size: 30px;
    order: 1;
  }
  
  .header p {
    margin: 5px 0;
    order: 2;
  }
  
  .header a {
    color: #0059ff;
    text-decoration: underline;
    order: 3;
  }
  
  .header a:hover {
    color: #ffffff;
  }
  
  .reservation-info, .guest-info, .payment-info {
    padding: 15px;
    border-bottom: 1px solid #ccc;
  }
  
  .reservation-info h2, .guest-info h3, .payment-info h3 {
    margin-top: 0;
    color: #333;
    
  }
  
  .details-grid {
    display: grid;
    grid-template-columns: auto 1fr;
    gap: 1px;
  }
  
  .details-grid p {
    margin: 5px 0;
    color: #555;
    gap: 2px;
  }
  

  /* booking details responsive*/
  @media (max-width: 768px) {
    .details-grid {
      grid-template-columns: 1fr;
    }
  
    .button-container {
      flex-direction: column;
      align-items: flex-start;
    }
  
    .button-container button {
      width: 100%;
      margin-bottom: 10px;
    }
  }
  
  @media (max-width: 480px) {
    .header h1 {
      font-size: 20px;
    }
  
    .header p,
    .details-grid p {
      font-size: 14px;
    }
  
    .booking-details {
      padding: 10px;
    }
  
    .button-container button {
      font-size: 14px;
      padding: 6px 12px;
    }
  }

  /* Responsive CSS for BookingList Component */

@media (max-width: 1111px) {
  .booking-list-container {
    padding: 10px;
  }

  .filter-header {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  .date-range-filter,
  .status-filter-buttons,
  .filter-container {
    width: 100%;
    margin-bottom: 10px;
  }

  .date-range-filter {
    display: flex;
    flex-direction: column;
  }

  .date-range-filter label,
  .date-range-filter input {
    margin-bottom: 5px;
    width: 100%;
  }

  .status-filter-buttons button,
  .filter-container button {
    width: 100%;
    margin-bottom: 5px;
  }

  .filter-button {
    padding: 10px;
    font-size: 14px;
  }

  .flight-animation {
    display: none;
  }

  ul {
    padding: 0;
  }

  .pagination {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }

  .pagination button {
    width: 48%;
  }
}

@media (max-width: 480px) {
  .date-range-filter label,
  .date-range-filter input {
    font-size: 14px;
  }

  .status-filter-buttons button,
  .filter-container button {
    font-size: 14px;
  }

  .pagination button {
    font-size: 14px;
    padding: 8px;
  }
}
/* Responsive CSS for BookingList Component for very small screens (width 250px) */

@media (max-width: 250px) {
  .booking-list-container {
    padding: 5px;
  }

  .filter-header {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  .date-range-filter,
  .status-filter-buttons,
  .filter-container {
    width: 100%;
    margin-bottom: 5px;
  }

  .date-range-filter {
    display: flex;
    flex-direction: column;
  }

  .date-range-filter label,
  .date-range-filter input {
    margin-bottom: 3px;
    width: 100%;
    font-size: 12px;
  }

  .status-filter-buttons button,
  .filter-container button {
    width: 100%;
    margin-bottom: 3px;
    font-size: 12px;
  }

  .filter-button {
    padding: 8px;
    font-size: 12px;
  }

  .flight-animation {
    display: none;
  }

  ul {
    padding: 0;
  }

  .pagination {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }

  .pagination button {
    width: 48%;
    font-size: 12px;
    padding: 6px;
  }
}
